				</view>
				<view class="newfl dis_flex oh1">
					<text class="fs12 myC9">{{item.content}}</text>
				</view>
			</view>
		</view>
		<button type="default" @click="jump" data-url="/pagesA_lzc/ceshi/ceshi">测试</button>
	</scroll-view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				news:[
					{
						hpic:'/static/images/logo.png',
						name:'李*',
						time:'17:03',
						content:'你不是想买商标么？我这有一个R标，你看看怎么样?',
						num:1
					},
					{
						hpic:'/static/images/logo.png',
						name:'李*',
						time:'17:03',
						content:'你不是想买商标么？我这有一个R标，你看看怎么样?',
						num:0
					}
				]
			}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
		}
